.foot-link{
    margin: 0px;
    color: white;
    font-size: 10pt;
    transition: 0.4s;
}
.foot-link>i{
    font-size: 14pt;
    margin-right: 10px;
}
.foot-link:hover{
    /* color: #787474; */
    color: white;
    text-decoration: none;
}
.store{
    width:130px;
    transition: 0.5s;
}
.store:hover{
    width:140px;
    position: relative;
}
.link-head{
    cursor: pointer;
    font-size:10px;
    color:#787474;
}
.link-head:hover{
    font-size: 11px;
    font-weight: bold;
    color: #787474;
    text-decoration: none;
}
.footer{
    background :linear-gradient(180deg, rgba(90,172,164,1) 0%, rgba(102,173,118,1) 100%) ;
}
