.menu-riwayat{
    border: 1px solid #FFFFFF;
    background-color:#FFFFFF;
    font-size : 12px;
    border-radius: 10%;
    cursor: pointer;
}

.menu-riwayat:hover{
    border: 1px solid #2E7D32;
    font-size : 12px;
    color: #2E7D32;
}
.menu-riwayat-aktif{
    border: 1px solid #2E7D32;
    font-size : 12px;
    color: #2E7D32;
}

.button-pilih{
    font-size:14px;
    width:300px;
    background-color:#FFFFFF;
    color:#2E7D32;
    /* padding:10px 3px; */
    cursor: pointer;
}
.button-pilih:hover{
    color:#827D7D ;
    background-color:#F5F4F4;
}
.riwayat-list>p{
    width: 300px;
    font-size: 14px;
    padding: 10px 5px;
    cursor: pointer;
    margin-bottom: 0px;
    color: #005005;
}
.riwayat-list>p:hover{
    background-color: #15CF74;
}



@media (max-width: 576px) { 
    .scroll-riwayat{
        overflow: auto;
        white-space: nowrap;
    }
   }