.bg-login{
    background: url("./images/bg-login.png") no-repeat; 
    background-size: auto;
    background-position: center; /* Center the image */
    height: auto;
    margin: 0px;
    padding: 0px;
  }
.margin-top-login{
    margin-top: 200px;
    margin-bottom: 200px;
  }
.title-login{
    font-family: 'Roboto', sans-serif;
    font-size: 28pt;
    color: #ffffff;
}
.login-frame{
    background-color: #FFFFFF;
    border: 1px solid #707070;
}
.button-login{
    border: 0px;
    background-color: #2E7D32;
    width: 100%;
    padding: 10px ;
    color: #FFFFFF;
    margin-top: 20px;
}
.login-download{
    color: #2E7D32;
}
.login-download:hover{
    color: #707070;
    text-decoration: none;
}